.banner {
  height: 15vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

.banner_number {
  font-size: 5rem;
  font-weight: 700;
  font-family: var(--font-secondary);
  color: var(--color-pine-green);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

@media screen and (max-width: 768px) {
  .banner {
    height: 10vh;
  }

  .banner_number {
    font-size: 3rem;
  }
}
